import * as C from '../types';

export const JoinInfo = (state = { isFetching: true }, action) => {
	switch (action.type) {
		case C.FETCH_JOIN_INFO:
			return { ...state, isFetching: true };
		case C.FINISHED_FETCH_JOIN_INFO:
			return { ...state, isFetching: false, payload: action.data };
		case C.ERROR_FETCH_JOIN_INFO:
			return {
				...state,
				payload: [],
				isFetching: false,
				error: action.error,
			};
		default:
			return state;
	}
};

export default JoinInfo;
